.homepage {
  display: flex;
  width: 100%;
  align-items: stretch;
}
.header {
  display: flex;
  position: fixed;
  width: 100%;
  height: 5rem;
}
.homepage .card {
  margin-top: 5rem;
  width: 100%;
}
.homepage .cardPlaces {
  display: flex;
  justify-content: space-around;
}
.homepage .cardPlace {
  min-width: 50%;
  display: flex;
  align-items: stretch;
  justify-content: center;
}
.imagen {
  width: 1000px;
}
.proyecto {
  background-color: rgb(229, 229, 229);
}
img {
  margin: 5px;
}